.cart-heading {
  color: slategray;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-heading h1 {
  margin: 0;
}
.cart-heading button {
  text-transform: none;
  font-family: serif;
}
.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: calc(80vh - (35px + 50px + 38px + 20px + 20px));
  overflow: auto;
}

.cart-items.checked {
  max-height: calc(40vh - 85px);
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 10%);
  padding: 1rem 0;
  margin: 1rem 0;
}

.cart-item h2 {
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.cart-total {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
}

.cart-no-items {
  padding: 20px;
  text-align: center;
}
.cart-no-items .title {
  color: gray;
  font-weight: bold;
}
.cart-no-items .message {
  color: darkgray;
}

.cart-action {
  height: 38px;
}

.summary {
  width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price {
  font-weight: bold;
  color: #8a2b06;
}

.amount {
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  color: #363636;
}

.actions {
  display: flex;
  flex-direction: row;
  align-items: center;
}

